import { Component, Prop, State, Element, h, Host } from '@stencil/core';

@Component({
  tag: 'bh-card',
  styleUrl: 'bh-card.css',
  shadow: true,
})
export class BhCard {
  @Element() el!: HTMLElement;

  /**
   * Whether to show the header section
   */
  @Prop() showHeader: boolean = true;

  /**
   * Whether to show the footer section
   */
  @Prop() showFooter: boolean = true;

  /**
   * Minimum height of the content area in pixels
   */
  @Prop() contentMinHeight: number = 200;

  /**
   * Placeholder text shown when no content is provided
   */
  @Prop() placeholderText: string = 'Card content slot';

  @State() hasContent: boolean = false;

  componentWillLoad() {
    this.checkForContent();
  }

  private checkForContent() {
    const children = Array.from(this.el.childNodes).filter(
      (node) =>
        (node.nodeType === Node.ELEMENT_NODE &&
          !(node as Element).hasAttribute('slot')) ||
        (node.nodeType === Node.TEXT_NODE && node.textContent?.trim())
    );
    this.hasContent = children.length > 0;
  }

  render() {
    const contentStyle = {
      minHeight: `${this.contentMinHeight}px`,
    };

    return (
      <Host>
        <div class="card">
          {this.showHeader && (
            <div class="card-header-slot">
              <slot name="header" />
            </div>
          )}
          <div class="card-content" style={contentStyle}>
            <slot />
            {!this.hasContent && (
              <div class="card-content-placeholder">
                <span class="card-placeholder-text">{this.placeholderText}</span>
              </div>
            )}
          </div>
          {this.showFooter && (
            <div class="card-footer-slot">
              <slot name="footer" />
            </div>
          )}
        </div>
      </Host>
    );
  }
}
